'use client';

import React, { useState, useEffect, useRef } from 'react';
import dynamic from 'next/dynamic';

// Dynamically import FlipBook to avoid SSR issues with canvas/DOMMatrix
const FlipBook = dynamic(() => import('@/components/FlipBook'), {
  ssr: false,
  loading: () => (
    <div className="flex items-center justify-center text-white">
      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-white mr-2"></div>
      Loading Viewer...
    </div>
  ),
});

import ControlPanel from '@/components/ControlPanel';
import SearchBar from '@/components/SearchBar';
import { useBookSound } from '@/hooks/useBookSound';
import { AnimatePresence, motion } from 'framer-motion';
import { useMediaQuery } from 'usehooks-ts';
import { Search } from 'lucide-react';

export default function Home() {
  // Initialize with the handbook file path
  const [file, setFile] = useState<string | File>('/handbook.pdf');
  const [currentPage, setCurrentPage] = useState(1);
  const [numPages, setNumPages] = useState(0);
  const [isAutoRolling, setIsAutoRolling] = useState(false);
  const { playFlip } = useBookSound();
  
  // View mode state
  const [isSinglePage, setIsSinglePage] = useState(false);
  const isMobile = useMediaQuery('(max-width: 768px)');

  // Theme state
  const [isDarkMode, setIsDarkMode] = useState(true);

  // Search state
  const [showSearch, setShowSearch] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchMatches, setSearchMatches] = useState<number[]>([]);
  const [currentMatchIndex, setCurrentMatchIndex] = useState(0);

  useEffect(() => {
    // Initialize theme based on document class or default to dark
    const prefersDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
    const initialDarkMode = document.documentElement.classList.contains('dark') || prefersDark;
    setIsDarkMode(initialDarkMode);
    if (initialDarkMode) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, []);

  const toggleTheme = () => {
    setIsDarkMode((prevMode) => {
      const newMode = !prevMode;
      if (newMode) {
        document.documentElement.classList.add('dark');
      } else {
        document.documentElement.classList.remove('dark');
      }
      return newMode;
    });
  };

  // Force single page on mobile
  useEffect(() => {
    if (isMobile) {
      setIsSinglePage(true);
    }
  }, [isMobile]);

  const handleReset = () => {
    // Reset to start
    setCurrentPage(1);
    setIsAutoRolling(false);
    window.location.reload();
  };

  const handleToggleView = () => {
    if (!isMobile) {
      setIsSinglePage(!isSinglePage);
    }
  };

  // Search handlers
  const handleSearch = (query: string) => {
    setSearchQuery(query);
    // TODO: Implement actual PDF text search using pdfjs
    // For now, just reset matches
    if (!query) {
      setSearchMatches([]);
      setCurrentMatchIndex(0);
    }
  };

  const handleSearchNext = () => {
    if (searchMatches.length > 0) {
      const nextIndex = (currentMatchIndex + 1) % searchMatches.length;
      setCurrentMatchIndex(nextIndex);
      // Navigate to page with match
      // TODO: Implement page navigation to match
    }
  };

  const handleSearchPrev = () => {
    if (searchMatches.length > 0) {
      const prevIndex = currentMatchIndex === 0 ? searchMatches.length - 1 : currentMatchIndex - 1;
      setCurrentMatchIndex(prevIndex);
      // Navigate to page with match
      // TODO: Implement page navigation to match
    }
  };

  return (
    <main className={`min-h-screen overflow-hidden relative transition-colors duration-300 ${isDarkMode ? 'bg-gray-900 text-white' : 'bg-gray-100 text-gray-900'}`}>
      {/* Branding Header */}
      <motion.div 
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, ease: "easeOut" }}
        className="absolute top-0 left-0 right-0 z-40 pt-8 pb-4 flex flex-col items-center pointer-events-none"
      >
        <div className="relative w-full flex justify-center pointer-events-auto">
          {/* Centered Title */}
          <div className="flex flex-col items-center text-center">
            <h1 className="text-3xl font-bold tracking-tight text-[#003366] dark:text-white mb-1">
              Harbour Tech Solutions
            </h1>
            <span className="text-xs font-bold text-[#D4AF37] tracking-[0.2em] uppercase">
              Official Handbook 2025
            </span>
          </div>
          
          {/* Search Button - Positioned to the right */}
          <button
            onClick={() => setShowSearch(!showSearch)}
            className="absolute right-8 top-1/2 -translate-y-1/2 p-2 hover:bg-white/10 rounded-full transition-colors bg-gray-800/50 backdrop-blur-sm border border-gray-700"
            title="Search PDF"
          >
            <Search className="w-5 h-5 text-[#D4AF37]" />
          </button>
        </div>

        {/* Search Bar - Below title */}
        <AnimatePresence>
          {showSearch && (
            <div className="mt-4 pointer-events-auto">
              <SearchBar
                onSearch={handleSearch}
                onNext={handleSearchNext}
                onPrev={handleSearchPrev}
                currentMatch={currentMatchIndex + 1}
                totalMatches={searchMatches.length}
                onClose={() => setShowSearch(false)}
              />
            </div>
          )}
        </AnimatePresence>
      </motion.div>

      <motion.div
        key="viewer"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="absolute inset-0 flex flex-col pt-20" // Added padding top for header
      >
        <FlipBookWrapper 
          file={file} 
          onReset={handleReset}
          isAutoRolling={isAutoRolling}
          setIsAutoRolling={setIsAutoRolling}
          isSinglePage={isSinglePage}
          onToggleView={handleToggleView}
          isDarkMode={isDarkMode}
          toggleTheme={toggleTheme}
        />
      </motion.div>
    </main>
  );
}

// Wrapper to handle the ref and state for the book
const FlipBookWrapper = ({ 
  file, 
  onReset,
  isAutoRolling,
  setIsAutoRolling,
  isSinglePage,
  onToggleView,
  isDarkMode,
  toggleTheme
}: { 
  file: string | File, 
  onReset: () => void,
  isAutoRolling: boolean,
  setIsAutoRolling: (v: boolean) => void,
  isSinglePage: boolean,
  onToggleView: () => void,
  isDarkMode: boolean,
  toggleTheme: () => void
}) => {
  const bookRef = useRef<any>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [numPages, setNumPages] = useState(0);
  const { playFlip } = useBookSound();

  const onPageChange = (e: any) => {
    setCurrentPage(e.data + 1);
    playFlip();
  };
  
  const onDocumentLoadSuccess = ({ numPages }: { numPages: number }) => {
    setNumPages(numPages);
  };

  // Auto-roll effect
  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isAutoRolling) {
      interval = setInterval(() => {
        if (bookRef.current && bookRef.current.pageFlip()) {
          const flip = bookRef.current.pageFlip();
          if (flip.getCurrentPageIndex() < flip.getPageCount() - 1) {
            flip.flipNext();
          } else {
            setIsAutoRolling(false); // Stop at end
          }
        }
      }, 2000); // Speedy roll
    }
    return () => clearInterval(interval);
  }, [isAutoRolling, setIsAutoRolling]);

  const handlePrev = () => {
    if (bookRef.current) {
      bookRef.current.pageFlip().flipPrev();
    }
  };

  const handleNext = () => {
    if (bookRef.current) {
      bookRef.current.pageFlip().flipNext();
    }
  };

  const handlePageInput = (page: number) => {
    if (bookRef.current) {
      // react-pageflip uses 0-based index
      bookRef.current.pageFlip().flip(page - 1);
    }
  };

  // Mouse wheel handler
  const lastWheelTime = useRef(0);
  const handleWheel = (e: React.WheelEvent) => {
    const now = Date.now();
    if (now - lastWheelTime.current < 500) return; // Debounce 500ms

    if (bookRef.current) {
      const flip = bookRef.current.pageFlip();
      if (e.deltaY > 0) {
        // Scroll down -> Next
        if (flip.getCurrentPageIndex() < flip.getPageCount() - 1) {
          flip.flipNext();
          lastWheelTime.current = now;
        }
      } else {
        // Scroll up -> Prev
        if (flip.getCurrentPageIndex() > 0) {
          flip.flipPrev();
          lastWheelTime.current = now;
        }
      }
    }
  };

  return (
    <>
      <div 
        className="flex-1 relative w-full h-full" 
        onWheel={handleWheel}
        onContextMenu={(e) => e.preventDefault()} // Restrict download/context menu
      >
        <RealFlipBook 
          file={file} 
          onPageChange={onPageChange} 
          onLoadSuccess={onDocumentLoadSuccess}
          ref={bookRef}
          usePortrait={isSinglePage}
        />
      </div>
      
      <ControlPanel
        currentPage={currentPage}
        numPages={numPages}
        onPrev={handlePrev}
        onNext={handleNext}
        onAutoRoll={() => setIsAutoRolling(!isAutoRolling)}
        isAutoRolling={isAutoRolling}
        onReset={onReset}
        onToggleView={onToggleView}
        isSinglePage={isSinglePage}
        onToggleTheme={toggleTheme}
        isDarkMode={isDarkMode}
        onPageInput={handlePageInput}
      />
    </>
  );
};

import RealFlipBook from '@/components/FlipBook';
